@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Libre+Barcode+39+Text&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Libre+Barcode+39+Text&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
*{
  margin:0px;
  padding:0px;
  box-sizing: border-box;
}
body{
  background-color: black;
  font-family: 'Press Start 2P', cursive;
}
.header{
    text-align: center;
    border: 4px solid #aaa;
    padding:5px;
    background-color:black;
    background-color: #650000;
}
.logo{
    margin:5px;
    color:#39FF14;
    text-transform: capitalize;
    font-size: 26px;
    font-family: 'Press Start 2P', cursive;
    
}
.moto{
    color:#aaa;
    font-family: 'Press Start 2P', cursive;
}
.home{
    color:#39FF14;
    font-family: 'Press Start 2P', cursive;
}
.nav >ul >li{
    display: inline;
    margin: 10px;
    padding: 5px;

}
.nav >ul >li:hover{
    color:red!important;
    

}
.nav{
display: flex;
justify-content: center;
padding:5px;
width:70%;
margin: auto;
font-family: 'Press Start 2P', cursive;

}
.link{
    text-decoration: none;
    transition: all .2s;

}
.link:hover{
    color:red;
    border-bottom: 2px solid red;
}
.robot{
    width:200px;
    height:200px;
}

.robo{
    border:10px solid #39FF14;
    padding:20px;
    background-color: grey;
    border-radius: 10px;
}
.feed{
    margin-top:10px;
    border-top: 4px solid #aaa;
}
.signout{
    text-decoration: none;
    text-transform: capitalize;
    color:gold;
    font-weight: lighter;
    font-size: mall;
    border:2px solid gold;
    padding:10px;
    margin-top:5px;
    background-color: #650000;
    border-radius: 10px;
}
.signout:hover{
    color:red;
    border:4px solid red;
    background-color: black;
}
.err{
    color:red;
    font-family: 'Press Start 2P', cursive;
    border:4px solid red;
    padding: 4px;
    text-align: center;
}

.msg{
    color: #da14FF;
    font-family: 'Press Start 2P', cursive;
    border:4px solid #dA14FF;
    padding: 4px;
    text-align: center;
}
.container{
border-bottom: #aaa 3px solid;
padding:3px;
display: flex;
flex-direction: row;
align-items: center;
margin:5px;
} 
.avauser{
    width:120px!important;
    height:120px!important;
    flex:.2 1;
    padding: 1px;
    border: 4px solid #aaa;
    transition:all  .3s;
}
.avauser:hover{
    border: 8px solid #39FF14;
    width:130px!important;
    height:130px!important;
    margin:10px;
}
.name{
    flex:.3 1;
    margin-left:30px;
    text-transform: capitalize;
    color:#DA14FF;
    font-family: 'Libre Barcode 39 Text', cursive;
    font-size: 70px;
}
.txt{
    color: #aaa!important;
    font-size: smaller;
}
.box{
    display:flex;
    justify-content: center;
    text-align: center;
    flex-direction: column;
    color:gold;
    margin: 10px;
    padding:20px;
}
.box>h3{
    margin:2px;
    text-transform: capitalize;
}
.online{
    font-size: xx-small;
    font-weight: lighter;
   color:red ;
}
.offline{
    font-size: xx-small;
    font-weight: lighter;
   color:#aaa ;
}
.handshake{
    display: flex;
    flex-direction: column;
    justify-content: center;
    border: 4px solid #aaa;
    border-radius:10px;
    align-items: center;
    padding:20px;
    background-color: indigo;
}
.answer{
    display:flex;
    flex-direction: row;
    margin: 10px;
}
.yes{
    flex:.5 1;
    text-align: center;
    margin-right:100px;
    color:gold;
    transition: all .2s;
}
.yes:hover{
    font-size: xx-large;

}
.no{
    color:red;
    transition: all .2s;
}
.no:hover{
    font-size: xx-large;
}
.userwelcome{
    border:4px solid #aaa;
    margin:10px;
    border-radius: 10px;
    background-color: #650000;
}
.targetimg{
    width:300px;
    height:300px;
    object-fit: contain;
    padding:10px;
    border:8px solid #39FF14;
    border-radius: 10px;
}
.targetbox{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding:20px;
}
.nameimg{

    margin-left:30px;
    text-transform: capitalize;
    color:#DA14FF;
    font-family: 'Libre Barcode 39 Text', cursive;
    font-size: 80px;
}
.form{
    background-color: black;
    display: flex;
    justify-content: center;
    margin-top: 5px;
}
.input{
    border:2px solid #39FF14;
    margin:5px;
    background-color: black;
    color:#39FF14;
    font-family: 'Press Start 2P', cursive;
    padding: 4px;
    border-radius: 5px;

}
.input:hover{
    border:3px solid red;
}
.label{
    color:#39FF14;
    font-family: 'Press Start 2P', cursive;
}
.label:hover{
    color:red;
}
.input:focus{
    color:red;
    font-family: 'Press Start 2P', cursive;
}
.btn{
    background-color: #DA14FF;
    width:180px;
    height:30px;
    border-radius: 5px;
    color:#aaa;
    font-family: 'Press Start 2P', cursive;
    border:none;
    transition: .5s all;
}
.btn:hover{
    width:182px;
    height:32px;
    font-size:20px
}
.button{
    display: flex;
    justify-content: center;
    margin:20px;
}
.rgForm{
    border: 5px solid #aaa;
    width:65%;
    margin: auto;
    margin-top:30px;
    padding: 20px;
}
.robot{
    width:100px;
    height:100px;
}
.robo{
    display: flex;
    justify-content: center;
    margin:40px;
}
.profile{
    color:#39FF14;
    font-family: 'Press Start 2P', cursive;
    display: flex;
    margin: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.profile:hover{
    color:#DA14FF;
}
.proinput{
    display: none;
}
.uploadicon{
    margin-right:10px;
    cursor: pointer;
}
.main{
    color:#aaa;
    font-family: 'Press Start 2P', cursive;
    text-align: center;
    text-transform: capitalize;
    border: 3px solid #aaa;
    padding: 10px;
}
.avatar{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.ava{
    margin-right: 20px;

}
.f{
    display: flex;
    flex-direction: row;
    align-items: center;
  
}
.level{
    color:yellow;
}
.homelink{
    display: flex;
    text-align: center;
    color:#DA14FF;
    padding:20px;
    border:4px solid #DA14FF;
    cursor: pointer;
    text-transform: uppercase;
    text-decoration: none;
}
.homelink:hover{
    color:red;
    border:4px solid #DA14FF;
}
