@import url('https://fonts.googleapis.com/css2?family=Libre+Barcode+39+Text&display=swap');

.container{
border-bottom: #aaa 3px solid;
padding:3px;
display: flex;
flex-direction: row;
align-items: center;
margin:5px;
} 
.avauser{
    width:120px!important;
    height:120px!important;
    flex:.2;
    padding: 1px;
    border: 4px solid #aaa;
    transition:all  .3s;
}
.avauser:hover{
    border: 8px solid #39FF14;
    width:130px!important;
    height:130px!important;
    margin:10px;
}
.name{
    flex:.3;
    margin-left:30px;
    text-transform: capitalize;
    color:#DA14FF;
    font-family: 'Libre Barcode 39 Text', cursive;
    font-size: 70px;
}
.txt{
    color: #aaa!important;
    font-size: smaller;
}
.box{
    display:flex;
    justify-content: center;
    text-align: center;
    flex-direction: column;
    color:gold;
    margin: 10px;
    padding:20px;
}
.box>h3{
    margin:2px;
    text-transform: capitalize;
}
.online{
    font-size: xx-small;
    font-weight: lighter;
   color:red ;
}
.offline{
    font-size: xx-small;
    font-weight: lighter;
   color:#aaa ;
}
.handshake{
    display: flex;
    flex-direction: column;
    justify-content: center;
    border: 4px solid #aaa;
    border-radius:10px;
    align-items: center;
    padding:20px;
    background-color: indigo;
}
.answer{
    display:flex;
    flex-direction: row;
    margin: 10px;
}
.yes{
    flex:.5;
    text-align: center;
    margin-right:100px;
    color:gold;
    transition: all .2s;
}
.yes:hover{
    font-size: xx-large;

}
.no{
    color:red;
    transition: all .2s;
}
.no:hover{
    font-size: xx-large;
}
.userwelcome{
    border:4px solid #aaa;
    margin:10px;
    border-radius: 10px;
    background-color: #650000;
}
.targetimg{
    width:300px;
    height:300px;
    object-fit: contain;
    padding:10px;
    border:8px solid #39FF14;
    border-radius: 10px;
}
.targetbox{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding:20px;
}
.nameimg{

    margin-left:30px;
    text-transform: capitalize;
    color:#DA14FF;
    font-family: 'Libre Barcode 39 Text', cursive;
    font-size: 80px;
}