@import url('https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap');

.form{
    background-color: black;
    display: flex;
    justify-content: center;
    margin-top: 5px;
}
.input{
    border:2px solid #39FF14;
    margin:5px;
    background-color: black;
    color:#39FF14;
    font-family: 'Press Start 2P', cursive;
    padding: 4px;
    border-radius: 5px;

}
.input:hover{
    border:3px solid red;
}
.label{
    color:#39FF14;
    font-family: 'Press Start 2P', cursive;
}
.label:hover{
    color:red;
}
.input:focus{
    color:red;
    font-family: 'Press Start 2P', cursive;
}
.btn{
    background-color: #DA14FF;
    width:180px;
    height:30px;
    border-radius: 5px;
    color:#aaa;
    font-family: 'Press Start 2P', cursive;
    border:none;
    transition: .5s all;
}
.btn:hover{
    width:182px;
    height:32px;
    font-size:20px
}
.button{
    display: flex;
    justify-content: center;
    margin:20px;
}
.rgForm{
    border: 5px solid #aaa;
    width:65%;
    margin: auto;
    margin-top:30px;
    padding: 20px;
}
.robot{
    width:100px;
    height:100px;
}
.robo{
    display: flex;
    justify-content: center;
    margin:40px;
}
.profile{
    color:#39FF14;
    font-family: 'Press Start 2P', cursive;
    display: flex;
    margin: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.profile:hover{
    color:#DA14FF;
}
.proinput{
    display: none;
}
.uploadicon{
    margin-right:10px;
    cursor: pointer;
}
.main{
    color:#aaa;
    font-family: 'Press Start 2P', cursive;
    text-align: center;
    text-transform: capitalize;
    border: 3px solid #aaa;
    padding: 10px;
}
.avatar{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.ava{
    margin-right: 20px;

}
.f{
    display: flex;
    flex-direction: row;
    align-items: center;
  
}
.level{
    color:yellow;
}
.homelink{
    display: flex;
    text-align: center;
    color:#DA14FF;
    padding:20px;
    border:4px solid #DA14FF;
    cursor: pointer;
    text-transform: uppercase;
    text-decoration: none;
}
.homelink:hover{
    color:red;
    border:4px solid #DA14FF;
}